`, "",{"version":3,"sources":["webpack://./src/components/Apps/DaemonSelectionModal.vue"],"names":[],"mappings":";AAoEA;CACA,kBAAA;AACA","sourcesContent":["<template>\n\t<div class=\"daemon-selection-modal\">\n\t\t<NcModal name=\"selectionModal\" :show=\"show\" @close=\"closeModal\">\n\t\t\t<div class=\"select-modal-body\">\n\t\t\t\t<h3>{{ t('app_api', 'Choose Deploy Daemon for {appName}', {appName: app.name }) }}</h3>\n\t\t\t\t<DaemonSelectionList\n\t\t\t\t\t:daemons.sync=\"daemons\"\n\t\t\t\t\t:default-daemon.sync=\"default_daemon_config\"\n\t\t\t\t\t:app-id=\"app.id\"\n\t\t\t\t\t@close=\"closeModal\" />\n\t\t\t</div>\n\t\t</NcModal>\n\t</div>\n</template>\n\n<script>\nimport NcModal from '@nextcloud/vue/dist/Components/NcModal.js'\n\nimport axios from '@nextcloud/axios'\nimport { generateUrl } from '@nextcloud/router'\nimport DaemonSelectionList from './DaemonSelectionList.vue'\n\nexport default {\n\tname: 'DaemonSelectionModal',\n\tcomponents: {\n\t\tNcModal,\n\t\tDaemonSelectionList,\n\t},\n\tprops: {\n\t\tshow: {\n\t\t\ttype: Boolean,\n\t\t\trequired: true,\n\t\t\tdefault: false,\n\t\t},\n\t\tapp: {\n\t\t\ttype: Object,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tselectDaemonModal: false,\n\t\t\tselectedDaemon: 'Cheese',\n\t\t\tdaemons: [],\n\t\t\tdefault_daemon_config: '',\n\t\t}\n\t},\n\n\tmounted() {\n\t\tthis.getAllDaemons()\n\n\t},\n\tmethods: {\n\t\tcloseModal() {\n\t\t\tthis.$emit('update:show', false)\n\t\t},\n\t\tgetAllDaemons() {\n\t\t\treturn axios.get(generateUrl('/apps/app_api/daemons'))\n\t\t\t\t.then(res => {\n\t\t\t\t\tthis.daemons = res.data.daemons\n\t\t\t\t\tthis.default_daemon_config = res.data.default_daemon_config\n\t\t\t\t})\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\n.select-modal-body h3 {\n\ttext-align: center;\n}\n\n</style>\n"],"sourceRoot":""}]);
